import Home from './components/Home'
import Menu from './components/Menu'
import Admin from './components/Admin'
import Login from './components/Login'
import Register from './components/Register'
import About from './components/about/about'

//二级路由
import Contact from './components/about/Contact'
import Delivery from './components/about/Delivery'
import History from './components/about/History'
import OrderingGulde from './components/about/OrderingGulde'

//三级路由
import Phone from './components/about/contact/Phone'
import PersonName from './components/about/contact/PersonName'


export const routes =[
    {path:'/',name:'homeLink',components:{
        default:Home,
        'orderingGulde':OrderingGulde,
        'delivery':Delivery,
        'history':History
    }},
    {path:'/menu',name:'menuLink',component:Menu},
    {path:'/admin',name:'adminLink',component:Admin,
    // beforeEach:(to,from,next)=>{ 
    //   路由独享守卫
      // alert('非登录状态，不能访问此页面');
      // next(false);
      //判断store.gettes.isLogin === false
    // if(to.path == '/login' || to.path == '/register'){
    //   next();
    // }else{
    //   alert('还没有登陆，请先登录');
    //   next('/login');
    // }
    //}
  },
    {path:'/login',name:'loginLink',component:Login},
    {path:'/register',name:'registerLink',component:Register},
    {path:'/about',redirect:'/about/contact',
    name:'aboutLink',
    component:About,
    children:[
      {path:'/about/contact',redirect:'/about/contact/phone',name:'contactLink',component:Contact,
      children:[
        {path:'/about/contact/phone',name:'phone',component:Phone},
        {path:'/about/contact/personName',name:'personName',component:PersonName},
      ]
    },
      {path:'/delivery',name:'deliveryLink',component:Delivery},
      {path:'/history',name:'historyLink',component:History},
      {path:'/orderingGulde',name:'orderingGuldeLink',component:OrderingGulde},
    ]
    },
    {path:'*',redirect:'/'}
  ]


